<template>
  <div>
    <v-container>
      <custom-snackbar ref="customSnackbar"></custom-snackbar>
      <v-card>
        <v-card-title class="headline font-weight-regular blue-grey white--text">Audit Record</v-card-title>
        <v-card-text>
          <v-form ref="form">
            <v-container fluid>
              <v-subheader>Encounter Details</v-subheader>
              <v-divider></v-divider>
              <v-layout row wrap>
                <v-flex xs12 md2 class="pr-1">
                  <v-text-field v-model="audit.id" label="Audit ID" :disabled="!newRecord"></v-text-field>
                </v-flex>
                <v-flex xs12 md5 class="pr-1">
                  <v-text-field v-model="auditorName" label="Auditor" :disabled="!newRecord"></v-text-field>
                </v-flex>
                <v-flex xs12 md5 class="pr-1">
                  <v-menu
                    ref="menuDatePicker"
                    :close-on-content-click="false"
                    v-model="menuDatePicker"
                    :nudge-right="40"
                    :return-value.sync="audit.audit_date"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    min-width="290px"
                  >
                    <v-text-field
                      slot="activator"
                      v-model="audit.audit_date"
                      label="Audit Date"
                      prepend-icon="event"
                      readonly
                      data-vv-name="Audit Date"
                      :error-messages="errors.collect('Audit Date')"
                    ></v-text-field>
                    <!-- v-validate = "{rules: { required: true }}" -->
                    <v-date-picker
                      v-model="audit.audit_date"
                      @input="$refs.menuDatePicker.save(audit.audit_date)"
                    ></v-date-picker>
                  </v-menu>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12 md4 class="pr-1">
                  <v-text-field
                    :value="currentPhysicianName"
                    label="MD Name"
                    :disabled="!newRecord"
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 md3 class="pr-1">
                  <v-text-field v-model="audit.npi" label="MD NPI" :disabled="!newRecord"></v-text-field>
                </v-flex>
                <v-flex xs12 md5 class="pr-1">
                  <v-text-field v-model="audit.facility" label="Facility" :disabled="!newRecord"></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12 md5 class="pr-1">
                  <v-text-field
                    v-model="audit.patient_name"
                    label="Patient Name"
                    :disabled="!newRecord"
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 md3 class="pr-1">
                  <v-text-field
                    v-model="audit.date_of_birth"
                    label="Date of Birth"
                    :disabled="!newRecord"
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 md2 class="pr-1">
                  <v-text-field v-model="audit.payor" label="Payer" :disabled="!newRecord"></v-text-field>
                </v-flex>
                <v-flex xs12 md2 class="pr-1">
                  <v-text-field
                    v-model="audit.service_date"
                    label="Date of Service"
                    :disabled="!newRecord"
                  ></v-text-field>
                </v-flex>
              </v-layout>
              <v-subheader>Coding Details</v-subheader>
              <v-divider></v-divider>
              <v-layout row wrap>
                <v-flex xs12>
                  <div
                    v-for="(detail) in audit.details"
                    :key="detail.cpt_billed"
                    class="grey lighten-5"
                  >
                    <v-container mt-2>
                      <v-layout row wrap>
                        <v-flex xs12 md4 class="pr-1">
                          <v-layout row wrap>
                            <v-flex xs12>
                              <v-text-field
                                v-model="detail.cpt_billed"
                                label="CPT Code Billed"
                                :disabled="!newRecord"
                              ></v-text-field>
                            </v-flex>
                          </v-layout>
                          <v-layout row wrap>
                            <v-flex xs12>
                              <v-text-field v-model="detail.cpt_audited" label="CPT Code Audited"></v-text-field>
                            </v-flex>
                          </v-layout>
                        </v-flex>
                        <v-flex xs12 md4 class="pr-1">
                          <v-layout row wrap>
                            <v-flex xs12>
                              <v-text-field
                                v-model="detail.modifier_billed"
                                label="Modifier Billed"
                                :disabled="!newRecord"
                              ></v-text-field>
                            </v-flex>
                          </v-layout>
                          <v-layout row wrap>
                            <v-flex xs12>
                              <v-text-field
                                v-model="detail.modifier_audited"
                                label="Modifier Audited"
                              ></v-text-field>
                            </v-flex>
                          </v-layout>
                        </v-flex>
                        <v-flex xs12 md4 class="pr-1">
                          <v-layout row wrap>
                            <v-flex xs12>
                              <v-text-field
                                :disabled="!newRecord"
                                v-model="detail.units_billed"
                                label="Units Billed"
                              ></v-text-field>
                            </v-flex>
                          </v-layout>
                          <v-layout row wrap>
                            <v-flex xs12>
                              <v-text-field v-model="detail.units_audited" label="Units Audited"></v-text-field>
                            </v-flex>
                          </v-layout>
                        </v-flex>
                      </v-layout>
                      <v-layout row wrap class="mt-2">
                        <v-flex xs12 md4 pl-2 pr-2>
                          <!-- WADE -->
                          <draggable
                            v-model="audit_data_table[detail.data_table_id]"
                            handle=".handle"
                          >
                            <transition-group>
                              <div
                                v-for="(element, index) in audit_data_table[detail.data_table_id]"
                                :key="element.billed"
                              >
                                <v-layout row wrap>
                                  <v-flex xs12 md2>
                                    <i class="material-icons handle">drag_handle</i>
                                  </v-flex>
                                  <v-flex xs12 md3 pr-2>
                                    <v-text-field v-model="element.billed" label="Billed"></v-text-field>
                                  </v-flex>
                                  <v-flex xs12 md2 pr-2>
                                    <i
                                      class="material-icons editor-icons"
                                      @click="copyCode(element.billed)"
                                    >file_copy</i>
                                  </v-flex>
                                  <v-flex xs12 md3>
                                    <v-text-field
                                      v-bind:id="'billedCode-' + detail.data_table_id + '-' + index"
                                      v-model="element.audited"
                                      label="Audited"
                                    ></v-text-field>
                                  </v-flex>
                                  <v-flex xs12 md2 pr-2>
                                    <i
                                      class="material-icons editor-icons"
                                      @click="element.billed === pasteCode(detail.data_table_id, index)"
                                    >content_paste</i>
                                  </v-flex>
                                </v-layout>
                              </div>
                            </transition-group>
                          </draggable>
                          <!-- WADE -->
                          <v-btn
                            small
                            color="success"
                            class="btn btn-secondary"
                            @click="addCode(detail.data_table_id)"
                          >Add ICD-10</v-btn>
                        </v-flex>
                        <v-flex xs12 md8>
                          Pass/Fail
                          <v-layout>
                            <v-flex xs12 class1="pr-1 pl-1">
                              <v-container>
                                <v-layout row wrap>
                                  <v-flex xs12 md7>
                                    <v-radio-group
                                      v-model="detail.pass"
                                      row
                                      @change="testPassFail(detail)"
                                    >
                                      <v-radio label="Pass - No Action" v-bind:value="true"></v-radio>
                                      <v-radio label="Fail - Action Required" v-bind:value="false"></v-radio>
                                    </v-radio-group>
                                  </v-flex>
                                  <v-flex xs12 md5 pr-1>
                                    <v-container>
                                      <v-layout row wrap>
                                        <v-flex xs12>
                                          <v-select
                                            v-model="detail.fail_reason"
                                            :items="failReasons"
                                            label="Fail Reason"
                                            v-if="detail.pass === false"
                                            @change="testFailReason(detail)"
                                          ></v-select>
                                        </v-flex>
                                      </v-layout>
                                      <v-layout row wrap>
                                        <v-flex xs12>
                                          <v-text-field
                                            v-model="detail.fail_reason_other"
                                            label="Other Reason"
                                            v-if="detail.fail_reason === 'Other'"
                                          ></v-text-field>
                                        </v-flex>
                                      </v-layout>
                                    </v-container>
                                  </v-flex>
                                </v-layout>
                              </v-container>
                            </v-flex>
                          </v-layout>
                          <v-layout row wrap>
                            <v-flex xs12 md2 class="pr-2">
                              <v-select
                                v-model="detail.standard_comments_id"
                                :items="commentTypes"
                                item-text="value"
                                item-value="id"
                                label="Standard Comments"
                              ></v-select>
                            </v-flex>
                            <v-flex xs12 md10 class="pr-2">
                              <v-textarea
                                v-model="detail.comments"
                                box
                                label="Comments"
                                auto-grow
                                rows="3"
                                style="padding-top:15px;"
                              ></v-textarea>
                            </v-flex>
                          </v-layout>
                        </v-flex>
                      </v-layout>
                    </v-container>
                  </div>
                </v-flex>
              </v-layout>
            </v-container>
            <v-btn @click="saveAudit()">Save</v-btn>
            <v-btn @click="cancelAudit()">Cancel</v-btn>
          </v-form>
        </v-card-text>
      </v-card>
    </v-container>
    <confirmation-dialog
      ref="confirmationDialog"
      title="Confirm"
      message="Are you sure you want to cancel your changes?"
    ></confirmation-dialog>
  </div>
</template>

<script>
import Config from "../config";
import ConfirmationDialog from "@/components/ConfirmationDialog";
import CustomSnackbar from "@/components/Snackbar";
import httpService from "../httpService";
import draggable from "vuedraggable";

// audit_date: new Date().toJSON()
export default {
  name: "Main",
  components: {
    ConfirmationDialog,
    CustomSnackbar,
    draggable
  },
  data: () => ({
    menuDatePicker: null,
    audit: {
      audit_date: ""
    },
    audit_data_table: [], // for draggable, doesn't like the nested loops/arrays
    copied_code: null // for copy/paste of icd-10 codes
  }),
  beforeMount() {
    // work on this one record
    //this.audit = this.audits.find(elem => elem.audit_id == this.$route.params.audit_id);
    this.init();
  },
  mounted() {},
  computed: {
    newRecord() {
      return false;
    },
    currentUser() {
      return this.$store.getters.CURRENT_USER;
    },
    auditors() {
      return this.$store.getters.ALL_USERS;
    },
    physicians() {
      return this.$store.getters.PHYSICIAN;
    },
    auditTypes() {
      return this.$store.getters.APPLICATION_LISTS.filter(
        row => row.keyword === "AUDIT_TYPE"
      ).map(row => row.value);
    },
    commentTypes() {
      return this.$store.getters.APPLICATION_LISTS.filter(
        row => row.keyword === "DIAGNOSIS_COMMENTS"
      ).sort((a, b) => (a.value > b.value ? 1 : b.value > a.value ? -1 : 0));
      //.map(row => row.value);
    },
    failReasons() {
      return this.$store.getters.APPLICATION_LISTS.filter(
        row => row.keyword === "AUDIT_FAIL_REASON"
      )
        .map(row => row.value)
        .sort();
    },
    currentPhysicianName() {
      if (this.audit.npi) {
        const indx = this.physicians.findIndex(
          obj => obj.npi === this.audit.npi
        );
        return (
          this.physicians[indx].last_name +
          ", " +
          this.physicians[indx].first_name
        );
      }
    },
    auditorName() {
      if (this.audit.auditor_id >= 0) {
        const indx = this.auditors.findIndex(
          obj => obj.id === this.audit.auditor_id
        );
        return this.auditors[indx].fullname;
      }
    }
  },
  methods: {
    async init() {
      httpService.get(
        `${Config.apiRoot}/audit/${this.$route.params.audit_id}`,
        (status, data) => {
          this.audit = data[0];

          // serialize icd10 fields after load
          if (this.audit.details) {
            for (let i = 0; i < this.audit.details.length; i++) {
              // this.audit.details[i].data_table_id = i;
              // this.audit.details[i].data_table = this.serializeFields(
              //   this.audit.details[i]
              // );

              //2 - working
              this.audit.details[i].data_table_id = i;
              this.audit_data_table[i] = this.serializeFields(
                this.audit.details[i]
              );
            }
          }

          if (this.audit.audit_date === null) {
            // default current date
            const today = new Date();
            this.audit.audit_date = `${today.getFullYear()}-${today.getMonth()}-${today.getDate()}`;
          }
          // fix fail reason
          if (
            this.audit.fail_reason &&
            this.audit.fail_reason.startsWith("Other")
          ) {
            const tmpFails = this.audit.fail_reason.split(" - ");
            this.audit.auditFailReason = tmpFails[0];
            this.audit.auditFailReasonOther = tmpFails[1];
          }
        }
      );
    },
    serializeFields(row) {
      // take individual fields (icd10_1 thru icd10_12) and convert it to an array
      // in - 1 detail row
      // returns JSON object
      let obj = [];
      for (let i = 1; i <= 12; i++) {
        const field1 = "icd10_" + i + "_billed";
        const field2 = "icd10_" + i + "_audited";
        if (row[field1] || row[field1]) {
          obj.push({ billed: row[field1], audited: row[field2] });
        }
      }
      return obj;
    },
    deserializeFields(id, row) {
      // take array and convert it back to fields (icd10_1 thru icd10_12)
      // in - id of the local array element to match
      // in - array
      // modifies this.audit
      for (let i = 0; i < 12; i++) {
        const field1 = "icd10_" + (i + 1) + "_billed";
        const field2 = "icd10_" + (i + 1) + "_audited";
        if (row[i]) {
          this.audit.details[id][field1] = row[i].billed;
          this.audit.details[id][field2] = row[i].audited;
        } else {
          // clear entries
          this.audit.details[id][field1] = null;
          this.audit.details[id][field2] = null;
        }
      }
      return true;
    },
    addCode(id) {
      if (this.audit_data_table[id].length < 12) {
        // up to 12 entries
        this.audit_data_table[id].push({ billed: "", audited: "" });
        this.$forceUpdate();
      }
    },
    copyCode(value) {
      this.copied_code = value;
    },
    pasteCode(group, item) {
      if (this.copied_code) {
        const field = "billedCode-" + group + "-" + item;
        document.getElementById(field).value = this.copied_code;
        this.copied_code = null;
      }
    },
    saveAudit: function() {
      this.$validator.validateAll().then(async result => {
        if (!result) {
          return;
        } else {
          // deserialize icd10 fields before save
          if (this.audit.details) {
            for (let i = 0; i < this.audit.details.length; i++) {
              this.deserializeFields(
                this.audit.details[i].data_table_id,
                this.audit_data_table[this.audit.details[i].data_table_id]
              );
            }
          }

          httpService.put(
            `${Config.apiRoot}/audit/${this.audit.id}`,
            this.audit,
            (status, data) => {
              if (data.success) {
                this.showMessage("success", "Record sucessfully saved");
              } else {
                this.showMessage(
                  "error",
                  "There was a problem saving your changes.  MSG - " +
                    data.message.originalError.info.message
                );
              }
            }
          );
        }
      });
    },
    cancelAudit: function() {
      this.$refs.confirmationDialog.confirmation().then(res => {
        if (res) {
          this.$router.go(-1);
        }
      });
    },
    testPassFail: function(objDetail) {
      if (objDetail.pass) {
        objDetail.fail_reason = "";
        objDetail.fail_reason_other = "";
      }
    },
    testFailReason: function(objDetail) {
      if (objDetail.fail_reason != "Other") {
        objDetail.fail_reason_other = "";
      }
    },
    showMessage: function(type, message) {
      this.$refs.customSnackbar.showSnackbar(message, type, 3000, true);
    }
  }
};
</script>

<style>
i:hover {
  cursor: pointer;
}
.v-subheader-donotuse {
  background-color: lightblue;
}

.container {
  padding: 0;
}

.handle {
  float: left;
  padding-top: 14px;
  padding-bottom: 8px;
}

.editor-icons {
  padding-top: 14px;
  padding-bottom: 8px;
}
</style>
